import {Alert} from "react-bootstrap";
import {useNavigate, useSearchParams} from "react-router-dom";
import {observer} from "mobx-react-lite";
import {useEffect} from "react";
import {sendActivation, state} from "./store";

export const Index = observer(() => {
    const [searchParams] = useSearchParams()
    const guid = searchParams.get("guid")
    const navigation = useNavigate()

    useEffect(() => {
        sendActivation(guid || "")
    }, []);

    useEffect(() => {
        console.log("ok")
    }, []);

    return <div className="d-flex justify-content-center align-items-center w-100 vh-100">
        {guid && state.activationIsSuccess &&
            <Alert variant="success">
                Ваш аккаунт успешно активирован. Вы будете перенаправлены на страницу входа.
            </Alert>
        }

        {(!guid || !state.activationIsSuccess) &&
            <Alert variant="danger">
                Ошибка активации. Попробуйте позднее или обратитесь в техническую поддержку.
            </Alert>
        }
    </div>
})
