import {FormProvider, useForm} from "react-hook-form";
import {Button, Form, Modal, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {observer} from "mobx-react-lite";
import {changePasswordAsync, modalState, switchVisibleChangePasswordModal} from "../store";
import {PasswordInput} from "../../../../common/component/input/password/PasswordInput";
import {SetPasswordInputs} from "../../../../common/component/input/set-password/SetPasswordInputs";

export const ChangePassword = observer(() => {
    const hookForm = useForm({
        mode: "all"
    });

    return <Modal show={modalState.isChangePasswordShow}>
                <FormProvider {...hookForm}>
                    <Form onSubmit={hookForm.handleSubmit(changePasswordAsync)}>
                        <ModalHeader>
                            <ModalTitle>Изменение пароля</ModalTitle>
                        </ModalHeader>
                        <ModalBody>
                            <PasswordInput
                                label="Старый пароль:"
                                name="oldPassword"/>
                            <SetPasswordInputs/>
                        </ModalBody>
                        <ModalFooter>
                            <Button variant="success" type="submit">Сохранить</Button>
                            <Button variant="danger" onClick={switchVisibleChangePasswordModal}>Закрыть</Button>
                        </ModalFooter>
                    </Form>
                </FormProvider>
            </Modal>
})
