import {useFormContext} from "react-hook-form";
import {Badge, FormControl, FormGroup, FormLabel, InputGroup} from "react-bootstrap";
import {ISetPasswordInputs} from "./ISetPasswordInputs";

export const SetPasswordInputs = (props: ISetPasswordInputs) => {
    const {register, watch, formState: {errors}} = useFormContext();

    return <>
        <FormGroup className="mb-2">
            <FormLabel>Пароль:</FormLabel>
            <FormControl {...register("password", {
                required: {value: true, message: "Поле обязательно к заполнению."},
                minLength: {value: 6, message: "Минимальная длина пароля 6 символов."},
                maxLength: {value: 12, message: "Максимальная длина пароля 12 символов."},
            })} type="password" placeholder="Введите пароль"/>
            {errors["password"]?.message &&
                <Badge bg="warning" className="mt-2">{errors["password"].message.toString()}</Badge>
            }
        </FormGroup>
        <FormGroup className="mb-2">
            <FormLabel>Подтвердите пароль:</FormLabel>
            <FormControl {...register("confirmPassword", {
                required: {value: true, message: "Поле обязательно к заполнению."},
                validate: (value) => {
                    if(watch("password") !== value)
                        return "Введенные пароли не совпадают."
                }
            })} type="password" placeholder="Повторите пароль"/>
            {errors["confirmPassword"]?.message &&
                <Badge bg="warning" className="mt-2">{errors["confirmPassword"].message.toString()}</Badge>
            }
        </FormGroup>
    </>
}
