import {action, observable} from "mobx";
import {ISubscribeManagement} from "./model/ISubscribeManagement";
import {getAsync} from "../../../../../common/fetch/getAsync";

const state = observable({state: null as unknown as ISubscribeManagement[]});

const getAccessPosition = action(async () => {
    await getAsync("/User/AccessPositions")
        .then(response => state.state = response.data.accessPositions)
})

export {state, getAccessPosition}
