import {observer} from "mobx-react-lite";
import {List} from "./component/list/List";
import {Button} from "react-bootstrap";
import {modalState, switchModalVisible} from "./component/add-edit-form/store";
import {AddEditForm} from "./component/add-edit-form/AddEditForm";

export const Index = observer(() => {
    return <>
        <div className="mb-3">
            <Button size="sm" onClick={() => switchModalVisible(0)}>Добавить сервер</Button>
        </div>
        <hr/>
        <List/>
        {modalState.addEditFormIsShow &&
            <AddEditForm/>
        }
    </>
})
