import {FormProvider, useForm} from "react-hook-form";
import {Button, Form} from "react-bootstrap";
import {TextInput} from "../../../common/component/input/text/TextInput";
import React from "react";
import {SetPasswordInputs} from "../../../common/component/input/set-password/SetPasswordInputs";
import {GoogleReCaptcha} from "react-google-recaptcha-v3";
import {sendForm} from "./store";
import {state} from "../../../common/appStore";
import {Link, Navigate, useNavigate} from "react-router-dom";
import {authState} from "../../../common/store/auth";

export const Index = () => {
    const hookForm = useForm()
    const navigation = useNavigate()

    if(authState.token !== null)
        return <Navigate to="/"/>

    return <div>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit(sendForm)}>
                <div className="d-flex flex-column justify-content-center align-items-center vh-100">
                    <Link to="/" className="text-center text-decoration-none text-black mb-4">
                        <h1 className="fw-bold">LockVPN</h1>
                        <h5>Регистрация</h5>
                    </Link>

                    <div className="d-flex flex-column align-items-center login-form">
                        <TextInput name="login"
                                   label="Логин:"
                                   placeholder="Введите логин" isRequired={true}/>
                        <TextInput name="email"
                                   label="Электронная почта:"
                                   placeholder="Введите электронную почту"
                                   isRequired={true}/>
                        <SetPasswordInputs/>

                        {state.mainStore?.captchaPublicKey &&
                            <div>
                                <GoogleReCaptcha onVerify={(token) => hookForm.setValue("token", token)}/>
                            </div>
                        }

                        <TextInput name="token" attributes={{className: "d-none"}}/>

                        <Button type="submit" variant="success" className="mt-2">
                            Создать аккаунт
                        </Button>
                        <Button type="submit" variant="secondary" className="mt-2" onClick={() => navigation("/Login")}>
                            Войти в аккаунт
                        </Button>
                    </div>
                </div>
            </Form>
        </FormProvider>
    </div>
}
