import {Button, Modal, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {modalState, switchVisibleSubscribeManagement} from "../../store";
import {getAccessPosition, state} from "./store";
import {useEffect} from "react";
import {observer} from "mobx-react-lite";

export const SubscribeManagement = observer(() => {

    useEffect(() => {
        getAccessPosition();
    }, [])

    return <Modal show={modalState.isSubscribeManagementShow}>
        <ModalHeader>
            <ModalTitle>Управление подпиской</ModalTitle>
        </ModalHeader>
        <ModalBody>
            {state.state &&
                state.state.map(position => {
                    return <div className="d-flex mt-2">
                        <div className="flex-fill text-center my-auto">{position.name}</div>
                        <div className="flex-fill text-center my-auto">{position.price} руб</div>
                        <div className="flex-fill text-center my-auto">
                            <Button variant="success">Купить</Button>
                        </div>
                    </div>
                })
            }
            {!state.state &&
                <span className="my-2">
                    Список пуст
                </span>
            }

        </ModalBody>
        <ModalFooter>
            <Button variant="danger" onClick={switchVisibleSubscribeManagement}>Закрыть</Button>
        </ModalFooter>
    </Modal>
})
