import {Profile} from "./component/Profile";
import {PaymentHistory} from "./component/PaymentHistory";
import {observer} from "mobx-react-lite";
import {getIndexAsync, modalState} from "./store";
import {ChangePassword} from "./component/ChangePassword";
import {useEffect} from "react";
import {SubscribeManagement} from "./component/subscribe-management/SubscribeManagement";

export const Index = observer(() => {

    useEffect(() => {
        getIndexAsync()
    }, [])

    return <div className="flex-fill">
        <div className="mb-3">
            <h4>Главная</h4>
        </div>
        <Profile/>
        <PaymentHistory/>

        {modalState.isChangePasswordShow &&
            <ChangePassword/>
        }

        {modalState.isSubscribeManagementShow &&
            <SubscribeManagement/>
        }
    </div>
})
