import {postAsync} from "../../../common/fetch/postAsync";
import {AxiosResponse} from "axios";
import {successToast} from "../../../common/toast/success";

const sendForm = async (form:any) => {
    const md5 = require('md5');

    form.password = md5(form.password)

    const afterPost = async (response: AxiosResponse<any, any>) => {
        successToast("Регистрация прошла успешно")
    }

    await postAsync<Object>("/User/Register", form, afterPost)
}

export {sendForm}
