import {IModal} from "./model/IModal";
import {action, observable} from "mobx";
import {getAsync} from "../../../common/fetch/getAsync";
import {IIndex} from "./model/IIndex";
import {postAsync} from "../../../common/fetch/postAsync";
import {successToast} from "../../../common/toast/success";

const state = observable({state: null as unknown as IIndex});

const modalState : IModal = observable({
    isSubscribeManagementShow: false,
    isChangePasswordShow: false
});

const getIndexAsync = action(async() => {
    await getAsync("/User/GetIndex")
        .then(response => state.state = response.data);
})

const changePasswordAsync = action(async (form: any) => {
    const md5 = require('md5');

    const afterPost = () => {
        modalState.isChangePasswordShow = false;
        successToast("Пароль успешно изменен.");
    }

    await postAsync<Object>("User/ChangePassword", {
        oldPassword: md5(form.oldPassword),
        password: md5(form.password)
    }, afterPost)
})

const switchVisibleChangePasswordModal = action(() => {
    if(modalState.isChangePasswordShow)
    {
        modalState.isChangePasswordShow = false;
    }
    else
    {
        modalState.isChangePasswordShow = true;
    }
})

const switchVisibleSubscribeManagement = action(() => {
    if(modalState.isSubscribeManagementShow)
    {
        modalState.isSubscribeManagementShow = false;
    }
    else
    {
        modalState.isSubscribeManagementShow = true;
    }
})

export {state, modalState, getIndexAsync, switchVisibleChangePasswordModal, switchVisibleSubscribeManagement, changePasswordAsync}
