import {observer} from "mobx-react-lite";
import React from "react";
import {Link, useSearchParams} from "react-router-dom";
import {MainContent} from "./component/MainContent";
import {PrivacyPolicy} from "./component/PrivacyPolicy";

export const Index = observer(() => {
    const [searchParams, setSearchParams] = useSearchParams()
    const page = searchParams.get("page")

    return <>
        <header className="d-flex justify-content-around align-items-center border-bottom" style={{height: "50px"}}>
            <div className="d-none d-sm-flex">
                <Link to="/" className="d-flex align-items-center px-3 h-100 text-decoration-none text-black">
                    Главная
                </Link>
                <div className="d-flex align-items-center px-3 h-100 text-decoration-none text-black"
                     style={{cursor: "pointer"}} onClick={() => setSearchParams("page=privacy_policy")}>
                    Политика конфиденциальности
                </div>
            </div>

            <div className="d-flex">
                <Link to="/register" className="d-flex align-items-center px-3 h-100 text-decoration-none text-black">
                    Регистрация
                </Link>
                <Link to="/login" className="d-flex align-items-center px-3 h-100 text-decoration-none text-black">
                    Личный кабинет
                </Link>
            </div>
        </header>
        <main className="d-flex flex-column justify-content-around align-items-center text-center">
            {!page && <MainContent/>}
            {page === "privacy_policy" && <PrivacyPolicy/>}
        </main>
        <footer className="d-sm-none d-flex justify-content-center border-top border-1 " style={{height: "50px"}}>
            <div className="d-flex align-items-center px-3 h-100 text-decoration-none text-black"
                 style={{cursor: "pointer"}} onClick={() => setSearchParams("page=privacy_policy")}>
                Политика конфиденциальности
            </div>
        </footer>
    </>
})
