import {action, observable} from "mobx";
import {getAsync} from "../../../../../common/fetch/getAsync";
import {IServer} from "../../model/IServer";

const modalState = observable({
    addEditFormIsShow: false
});

const state = observable({server: null as unknown as IServer});

const getVpnServerAsync = action(async (id: number) => {
    getAsync("")
})

const switchModalVisible = action(async (id ?: number) => {
    if(id !== undefined)
    {
        await getVpnServerAsync(id)
    }

    modalState.addEditFormIsShow = !modalState.addEditFormIsShow;
})

export {state, modalState, getVpnServerAsync, switchModalVisible}
