import {Nav, Offcanvas, OffcanvasBody, OffcanvasHeader, OffcanvasTitle} from "react-bootstrap";
import {Link} from "react-router-dom";
import {
    FileBinaryFill,
    HddRackFill,
    HouseDoorFill,
    InfoCircleFill,
    List,
    ListTask,
    PeopleFill
} from "react-bootstrap-icons";
import {useState} from "react";
import {observer} from "mobx-react-lite";
import {authState} from "../../store/auth";
import {userRole} from "../../store/model/IUser";

export const LeftMenu = observer(() => {
    const [isShow, setShow] = useState(false);

    const setHide = () => setShow(false)

    return <>
        {/*Обычное меню - Начало*/}
        <div className="nav d-lg-flex d-none flex-column justify-content-between py-2 left-menu">
            <Nav className="flex-column mx-4">
                <Link className="nav-link d-flex justify-content-between align-items-center" to="/">
                    <HouseDoorFill className="me-2"/>
                    <span className="w-100 text-center">Главная</span>
                </Link>
                <Link className="nav-link d-flex justify-content-between align-items-center" to="/help">
                    <InfoCircleFill className="me-2"/>
                    <span className="w-100 text-center">Помощь</span>
                </Link>
            </Nav>

            {authState.user?.role === userRole.admin &&
                <Nav className="flex-column mx-4">
                    <hr/>
                    <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/users">
                        <PeopleFill className="me-2"/>
                        <span className="w-100 text-center">Пользователи</span>
                    </Link>
                    <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/servers">
                        <HddRackFill className="me-2"/>
                        <span className="w-100 text-center">Сервера</span>
                    </Link>
                    <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/logs">
                        <ListTask className="me-2"/>
                        <span className="w-100 text-center">Логи</span>
                    </Link>
                    <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/file">
                        <FileBinaryFill className="me-2"/>
                        <span className="w-100 text-center">Файлы</span>
                    </Link>
                </Nav>
            }

            <div className="text-center">
                <span>LockVpn 2023</span>
            </div>
        </div>
        {/*Обычное меню - Конец*/}

        {/*Мобильное меню - Начало*/}
        <div className="d-block d-lg-none menu-button" onClick={() => setShow(true)}>
            <List width={35} height={35}/>
        </div>

        <Offcanvas show={isShow} onHide={setHide}>
            <OffcanvasHeader closeButton>
                <OffcanvasTitle>Меню</OffcanvasTitle>
            </OffcanvasHeader>
            <OffcanvasBody>
                    <Nav className="flex-column mx-5">
                        <Link className="nav-link d-flex justify-content-center align-items-center" to="/" onClick={setHide}>
                            <HouseDoorFill className="me-3"/>
                            <span>Главная</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-center align-items-center" to="/help" onClick={setHide}>
                            <InfoCircleFill className="me-3"/>
                            <span>Помощь</span>
                        </Link>
                    </Nav>
            </OffcanvasBody>
        </Offcanvas>
        {/*Мобильное меню - Конец*/}
    </>
})
