import {Badge, Button} from "react-bootstrap";
import {state, switchVisibleChangePasswordModal, switchVisibleSubscribeManagement} from "../store";
import {observer} from "mobx-react-lite";

export const Profile = observer(() => {
    return <>
        <div>
            <h6>Профиль:</h6>
        </div>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="flex-fill">
                <div>
                    <span>Логин:</span> {state.state?.login}
                </div>

                <div>
                    <span>Дата регистрации: </span>{new Date(state.state?.registerDate).toLocaleDateString()}
                </div>
            </div>
            <div className="flex-fill">
                <div>
                    <span>Доступ: </span>

                    {new Date(state.state?.endAccessDate) > new Date() &&
                        <Badge bg="success">Активен (до {new Date(state.state?.endAccessDate).toLocaleDateString()})</Badge>
                    }

                    {new Date(state.state?.endAccessDate) <= new Date() &&
                        <Badge bg="danger">Активен (до {new Date(state.state?.endAccessDate).toLocaleDateString()})</Badge>
                    }
                </div>
            </div>
        </div>
        <div className="d-flex mt-2">
            <Button variant="primary" size="sm" onClick={switchVisibleChangePasswordModal} className="me-2">Сменить пароль</Button>
            <Button variant="primary" size="sm" onClick={switchVisibleSubscribeManagement} className="me-2">Управление подпиской</Button>
        </div>
        <hr/>
    </>
})
