import {observer} from "mobx-react-lite";
import {Button, Form, Modal, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {FormProvider, useForm} from "react-hook-form";
import {modalState, state, switchModalVisible} from "./store";
import {TextInput} from "../../../../../common/component/input/text/TextInput";
import {useEffect} from "react";

export const AddEditForm = observer(() => {
    const hookForm = useForm();

    useEffect(() => {

    }, []);

    return <Modal show={modalState.addEditFormIsShow}>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit((form) => console.log(form))}>
                <ModalHeader>
                    <ModalTitle>
                        {state.server?.id === undefined &&
                            <>Добавить новый сервер</>
                        }
                        {state.server?.id > 0 &&
                            <>Редактировать сервер</>
                        }
                    </ModalTitle>
                </ModalHeader>
                <ModalBody>
                    <TextInput
                        label="Имя сервера:"
                        name="name"
                        defaultValue={state.server?.name}/>
                    <TextInput
                        label="IP:"
                        name="ip"
                        defaultValue={state.server?.ip}/>
                    <TextInput
                        label="Порт:"
                        name="port"
                        defaultValue={state.server?.port.toString()}/>
                </ModalBody>
                <ModalFooter>
                    <Button variant="success" type="submit">Сохранить</Button>
                    <Button variant="danger" onClick={() => switchModalVisible()}>Закрыть</Button>
                </ModalFooter>
            </Form>
        </FormProvider>
    </Modal>
})
